import React, { useState, useEffect } from 'react';
import {
    Box,
    Button,
    Typography,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    CircularProgress,
    Alert,
    Checkbox,
    FormControlLabel,
    FormGroup,
    TextField,
    InputAdornment,
    IconButton,
    Tooltip,
    Chip,
    Avatar,
    TablePagination,
    TableSortLabel,
} from '@mui/material';
import {
    Search as SearchIcon,
    Refresh as RefreshIcon,
    Person as PersonIcon,
    Business as BusinessIcon,
    Email as EmailIcon,
} from '@mui/icons-material';
import axios from 'axios';

const ADUserImport = () => {
    const [users, setUsers] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');
    const [selectedUsers, setSelectedUsers] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [orderBy, setOrderBy] = useState('username');
    const [order, setOrder] = useState('asc');

    const fetchADUsers = async () => {
        setLoading(true);
        setError('');
        try {
            const response = await axios.post('/api/fetch-ad-users');
            setUsers(response.data.users);
            setSelectedUsers(new Array(response.data.users.length).fill(true));
        } catch (err) {
            setError('خطا در دریافت اطلاعات کاربران از Active Directory');
            console.error(err);
        }
        setLoading(false);
    };

    const handleSelectAll = (event) => {
        setSelectedUsers(new Array(users.length).fill(event.target.checked));
    };

    const handleSelectUser = (index) => {
        const newSelected = [...selectedUsers];
        newSelected[index] = !newSelected[index];
        setSelectedUsers(newSelected);
    };

    const handleSearch = (event) => {
        setSearchTerm(event.target.value);
        setPage(0);
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    const handleSort = (property) => {
        const isAsc = orderBy === property && order === 'asc';
        setOrder(isAsc ? 'desc' : 'asc');
        setOrderBy(property);
    };

    const filteredUsers = users.filter(user => 
        user.username.toLowerCase().includes(searchTerm.toLowerCase()) ||
        user.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        user.department.toLowerCase().includes(searchTerm.toLowerCase()) ||
        user.email.toLowerCase().includes(searchTerm.toLowerCase())
    );

    const sortedUsers = filteredUsers.sort((a, b) => {
        const isAsc = order === 'asc';
        if (orderBy === 'username') {
            return isAsc ? a.username.localeCompare(b.username) : b.username.localeCompare(a.username);
        } else if (orderBy === 'name') {
            return isAsc ? a.name.localeCompare(b.name) : b.name.localeCompare(a.name);
        } else if (orderBy === 'department') {
            return isAsc ? a.department.localeCompare(b.department) : b.department.localeCompare(a.department);
        } else if (orderBy === 'email') {
            return isAsc ? a.email.localeCompare(b.email) : b.email.localeCompare(a.email);
        }
        return 0;
    });

    const paginatedUsers = sortedUsers.slice(
        page * rowsPerPage,
        page * rowsPerPage + rowsPerPage
    );

    const importUsers = async () => {
        setLoading(true);
        setError('');
        setSuccess('');
        
        const usersToImport = users.filter((_, index) => selectedUsers[index]);
        
        try {
            const response = await axios.post('/api/import-ad-users', {
                users: usersToImport
            });
            setSuccess(response.data.message);
            setUsers([]);
            setSelectedUsers([]);
        } catch (err) {
            setError('خطا در وارد کردن کاربران');
            console.error(err);
        }
        setLoading(false);
    };

    return (
        <Box sx={{ p: 3 }}>
            <Typography variant="h5" gutterBottom>
                وارد کردن کاربران از Active Directory
            </Typography>

            {error && (
                <Alert severity="error" sx={{ mb: 2 }}>
                    {error}
                </Alert>
            )}

            {success && (
                <Alert severity="success" sx={{ mb: 2 }}>
                    {success}
                </Alert>
            )}

            <Box sx={{ mb: 2, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <TextField
                    placeholder="جستجو..."
                    value={searchTerm}
                    onChange={handleSearch}
                    sx={{ width: 300 }}
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <SearchIcon />
                            </InputAdornment>
                        ),
                    }}
                />
                <Box>
                    <Tooltip title="بارگذاری مجدد">
                        <IconButton onClick={fetchADUsers} disabled={loading} sx={{ mr: 2 }}>
                            <RefreshIcon />
                        </IconButton>
                    </Tooltip>
                    {users.length > 0 && (
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={importUsers}
                            disabled={loading || !selectedUsers.some(selected => selected)}
                        >
                            وارد کردن کاربران انتخاب شده
                        </Button>
                    )}
                </Box>
            </Box>

            {loading ? (
                <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
                    <CircularProgress />
                </Box>
            ) : (
                <TableContainer component={Paper} elevation={3}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell padding="checkbox">
                                    <FormControlLabel
                                        control={
                                            <Checkbox
                                                checked={selectedUsers.every(selected => selected)}
                                                indeterminate={selectedUsers.some(selected => selected) && !selectedUsers.every(selected => selected)}
                                                onChange={handleSelectAll}
                                            />
                                        }
                                        label="انتخاب همه"
                                    />
                                </TableCell>
                                <TableCell>
                                    <TableSortLabel
                                        active={orderBy === 'username'}
                                        direction={orderBy === 'username' ? order : 'asc'}
                                        onClick={() => handleSort('username')}
                                    >
                                        نام کاربری
                                    </TableSortLabel>
                                </TableCell>
                                <TableCell>
                                    <TableSortLabel
                                        active={orderBy === 'name'}
                                        direction={orderBy === 'name' ? order : 'asc'}
                                        onClick={() => handleSort('name')}
                                    >
                                        نام
                                    </TableSortLabel>
                                </TableCell>
                                <TableCell>
                                    <TableSortLabel
                                        active={orderBy === 'department'}
                                        direction={orderBy === 'department' ? order : 'asc'}
                                        onClick={() => handleSort('department')}
                                    >
                                        دپارتمان
                                    </TableSortLabel>
                                </TableCell>
                                <TableCell>
                                    <TableSortLabel
                                        active={orderBy === 'email'}
                                        direction={orderBy === 'email' ? order : 'asc'}
                                        onClick={() => handleSort('email')}
                                    >
                                        ایمیل
                                    </TableSortLabel>
                                </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {paginatedUsers.map((user, index) => (
                                <TableRow key={user.username} hover>
                                    <TableCell padding="checkbox">
                                        <Checkbox
                                            checked={selectedUsers[index]}
                                            onChange={() => handleSelectUser(index)}
                                        />
                                    </TableCell>
                                    <TableCell>
                                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                            <Avatar sx={{ width: 32, height: 32, mr: 1, bgcolor: 'primary.main' }}>
                                                <PersonIcon />
                                            </Avatar>
                                            {user.username}
                                        </Box>
                                    </TableCell>
                                    <TableCell>{user.name}</TableCell>
                                    <TableCell>
                                        <Chip
                                            icon={<BusinessIcon />}
                                            label={user.department || 'نامشخص'}
                                            variant="outlined"
                                            size="small"
                                        />
                                    </TableCell>
                                    <TableCell>
                                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                            <EmailIcon sx={{ mr: 1, color: 'text.secondary' }} />
                                            {user.email || 'نامشخص'}
                                        </Box>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                    <TablePagination
                        rowsPerPageOptions={[5, 10, 25]}
                        component="div"
                        count={filteredUsers.length}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        labelRowsPerPage="تعداد در هر صفحه"
                        labelDisplayedRows={({ from, to, count }) => `${from}-${to} از ${count}`}
                    />
                </TableContainer>
            )}
        </Box>
    );
};

export default ADUserImport; 